$sub-color: grey;
$main-color: black;

@mixin shrinkLable {
  top: -16px;
  font-size: 12px;
  color: $main-color;
}

.group {
  position: relative;
  margin: 45px 0;

  .form-input {
    background: none;
    background-color: white;
    color: $sub-color;
    font-size: 18px;
    padding: 10px 10px 5px;
    display: block;
    width: 100%;
    border: none;
    border-bottom: 1px solid $sub-color;
    margin: 25px 0;

    &:focus {
      outline: none;
      background-color: rgb(212, 235, 243);
    }
    &:focus ~ .form-input-label {
      @include shrinkLable();
    }
  }
  .form-input-label {
    color: $sub-color;
    font-size: 16px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 10px;
    top: 10px;
    transition: 300ms ease all;

    &.shrink {
      @include shrinkLable();
    }
  }
}
